import zan from '@/static/mbcImg/news/1.png';
import fensi from '@/static/mbcImg/news/2.png';
import shou from '@/static/mbcImg/news/3.png';
import news from '@/static/mbcImg/news/4.png';
import { mapMutations,mapGetters } from 'vuex';
export default {
	data() {
		return {
			zan:zan,
			fensi:fensi,
			shou:shou,
			news:news,
			list:[],
			num:[],
			shu:[]
		}
	},
	mounted() {

	},
	watch: {
		GET_NEWS: {
			handler(a, b) {
				console.log(a, b)
				this.shu = a.num;
				console.log(this.shu)
			},
			deep: true
		},
	
	},
	methods: {
		gotoPraisedAndCollection(e){
			if (uni.getStorageSync('landRegist')) {
				let landRegistLG = JSON.parse(uni.getStorageSync('landRegist')); // 读取缓存的用户信息
				console.log(landRegistLG.user.id);
				// let params = {}; // 请求总数居时 参数为空
				uni.showLoading({ // 展示loading
					title: '加载中'
				});
				uni.request({
					url: this.api2 + '/rest-rp/notice/readByType?userId='+landRegistLG.user.id+'&type=0', //接口地址。
					// data: params,
					method: 'GET',
					header: {
						Authorization: "Bearer " + landRegistLG.token //将token放到请求头中
					},
					success: (response) => {
						uni.hideLoading();
						console.log(response.data);
						this.Unread();
						console.log('获赞与收藏');
						uni.navigateTo({
							url: '/modules/news/newsPraisedAndCollection/news-PraisedAndCollection?id=' + e,
						});
					},
					fail: (error) => {
						uni.hideLoading(); // 隐藏 loading
						uni.showToast({
							title: '网络繁忙，请稍后',
							icon: 'none',
							duration: 1000
						});
						console.log(error, '网络繁忙，请稍后');
					}
				});
			}
			
		},
		gotomyFocusonfans(e){
			if (uni.getStorageSync('landRegist')) {
				let landRegistLG = JSON.parse(uni.getStorageSync('landRegist')); // 读取缓存的用户信息
				console.log(landRegistLG.user.id);
				// let params = {}; // 请求总数居时 参数为空
				uni.showLoading({ // 展示loading
					title: '加载中'
				});
				uni.request({
					url: this.api2 + '/rest-rp/notice/readByType?userId='+landRegistLG.user.id+'&type=1', //接口地址。
					// data: params,
					method: 'GET',
					header: {
						Authorization: "Bearer " + landRegistLG.token //将token放到请求头中
					},
					success: (response) => {
						uni.hideLoading();
						console.log(response.data);
						this.Unread();
						console.log('已关注 粉丝');
						uni.navigateTo({
							url: '/modules/myCompany/myFocusonfans/my-Focusonfans?id=' + e,
						});
					},
					fail: (error) => {
						uni.hideLoading(); // 隐藏 loading
						uni.showToast({
							title: '网络繁忙，请稍后',
							icon: 'none',
							duration: 1000
						});
						console.log(error, '网络繁忙，请稍后');
					}
				});
			}
			
		},
		gotosystem(){
			if (uni.getStorageSync('landRegist')) {
				let landRegistLG = JSON.parse(uni.getStorageSync('landRegist')); // 读取缓存的用户信息
				console.log(landRegistLG.user.id);
				// let params = {}; // 请求总数居时 参数为空
				uni.showLoading({ // 展示loading
					title: '加载中'
				});
				uni.request({
					url: this.api2 + '/rest-rp/notice/readByType?userId='+landRegistLG.user.id+'&type=3', //接口地址。
					// data: params,
					method: 'GET',
					header: {
						Authorization: "Bearer " + landRegistLG.token //将token放到请求头中
					},
					success: (response) => {
						uni.hideLoading();
						console.log(response.data);
						this.Unread();
						console.log('系统通知');
						uni.navigateTo({
							url: '/modules/news/newssystem/news-systemlist',
						});
					},
					fail: (error) => {
						uni.hideLoading(); // 隐藏 loading
						uni.showToast({
							title: '网络繁忙，请稍后',
							icon: 'none',
							duration: 1000
						});
						console.log(error, '网络繁忙，请稍后');
					}
				});
			}
			
		},
		Unread(){//消息数量
			if (uni.getStorageSync('landRegist')) {
				let landRegistLG = JSON.parse(uni.getStorageSync('landRegist')); // 读取缓存的用户信息
				console.log(landRegistLG.user.id);
				// let params = {}; // 请求总数居时 参数为空
				uni.showLoading({ // 展示loading
					title: '加载中'
				});
				uni.request({
					url: this.api2 + '/rest-rp/notice/index?userId='+landRegistLG.user.id, //接口地址。
					// data: params,
					method: 'GET',
					header: {
						Authorization: "Bearer " + landRegistLG.token //将token放到请求头中
					},
					success: (response) => {
						uni.hideLoading();
						console.log(response.data);
						this.num = response.data.content
						this.$store.commit('setnum', this.num);
						// this.lists = response.data.content.instKeys
					},
					fail: (error) => {
						uni.hideLoading(); // 隐藏 loading
						uni.showToast({
							title: '网络繁忙，请稍后',
							icon: 'none',
							duration: 1000
						});
						console.log(error, '网络繁忙，请稍后');
					}
				});
			}
		},
	},
	computed: {
		i18n() {
		  return this.$t('My')  
		},
		...mapGetters(['GET_NEWS'])
	},
}
